<template>
  <div class="club-page">
    <!-- 社团详情模态框 -->
    <div v-if="selectedClub" class="club-modal" @click="closeModal">
      <div class="modal-content" @click.stop>
        <button class="close-btn" @click="closeModal">×</button>
        
        <div class="modal-header">
          <h2 class="club-title">{{ selectedClub.name }}</h2>
          <div class="club-meta">
            <span class="category-tag">{{ selectedClub.category }}</span>
            <span class="year">{{ selectedClub.year }}</span>
          </div>
        </div>
        
        <div class="modal-body">
          <!-- 图片轮播 -->
          <div class="image-carousel">
            <div class="carousel-container">
              <img 
                :src="selectedClub.images[currentImageIndex]" 
                :alt="selectedClub.name"
                class="carousel-image"
              />
              <button v-if="selectedClub.images.length > 1" class="carousel-btn prev" @click="prevImage">‹</button>
              <button v-if="selectedClub.images.length > 1" class="carousel-btn next" @click="nextImage">›</button>
              <div v-if="selectedClub.images.length > 1" class="carousel-indicators">
                <span 
                  v-for="(img, index) in selectedClub.images" 
                  :key="index"
                  :class="{ active: index === currentImageIndex }"
                  @click="currentImageIndex = index"
                ></span>
              </div>
            </div>
          </div>
          
          <!-- 社团详情 -->
          <div class="club-details">
            <div class="detail-section">
              <h3>Club Overview</h3>
              <p class="animated-text">{{ selectedClub.description }}</p>
            </div>
            
            <div class="detail-grid">
              <div class="detail-item">
                <h4>Activities</h4>
                <p>{{ selectedClub.activities }}</p>
              </div>
              <div class="detail-item">
                <h4>Duration</h4>
                <p>{{ selectedClub.duration }}</p>
              </div>
              <!-- Skills removed from details UI per request -->
            </div>
          </div>
        </div>
      </div>
    </div>
    
    <!-- 主社团列表 -->
    <div class="club-container">
      <div class="page-header">
        <h1 class="page-title">My Clubs</h1>
        <p class="page-subtitle">Student organizations and extracurricular activities</p>
        <div class="title-underline"></div>
      </div>
      
      <!-- 社团网格 -->
      <div class="club-grid">
        <div 
          v-for="club in clubs" 
          :key="club.id"
          class="club-card"
          @click="openClub(club)"
        >
          <div class="card-image">
            <img :src="club.images[0]" :alt="club.name" />
            <div v-if="club.featured" class="featured-badge">Featured</div>
            <div class="card-overlay">
              <span class="view-details">View Details</span>
            </div>
          </div>
          <div class="card-content">
            <h3 class="card-title">{{ club.shortName }}</h3>
            <p class="card-description">{{ club.description.substring(0, 120) }}...</p>
            <div class="card-meta">
              <span class="card-category">{{ club.category }}</span>
              <span class="card-year">{{ club.year }}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Club',
  data() {
    return {
      clubs: [
        {
          id: 1,
          name: 'Business Club',
          shortName: 'Business Club',
          description: 'A student-led organization focused on business education, entrepreneurship, and professional development. We organize workshops, networking events, and business competitions to help students develop practical business skills.',
          category: 'Business',
          year: '2023-2024',
          duration: '1 year',
          activities: 'Business workshops, networking events, case competitions, guest speaker sessions',
          skills: ['Leadership', 'Teamwork', 'Public Speaking', 'Business Strategy'],
          images: [
            require('@/assets/club/bc/DSC02670.webp'),
            require('@/assets/club/bc/DSC02674.webp'),
            require('@/assets/club/bc/DSC02687.webp'),
            require('@/assets/club/bc/DSC02690.webp'),
            require('@/assets/club/bc/DSC02691.webp')
          ],
          featured: true
        },
        {
          id: 2,
          name: 'Dream Architect Club',
          shortName: 'Dream Architect',
          description: 'A creative community for students interested in architecture, design, and urban planning. We explore innovative design concepts and participate in architectural competitions.',
          category: 'Design',
          year: '2023-2024',
          duration: '1 year',
          activities: 'Design workshops, architectural tours, model building, design competitions',
          skills: ['Creative Design', 'Spatial Planning', 'Model Making', 'CAD Software'],
          images: [
            require('@/assets/club/da/da.webp')
          ],
          featured: false
        },
        {
          id: 3,
          name: 'Student Union',
          shortName: 'Student Union',
          description: 'The official student government organization representing student interests and organizing campus-wide events. We work to improve student life and foster campus community.',
          category: 'Leadership',
          year: '2023-2024',
          duration: '1 year',
          activities: 'Student government meetings, campus events planning, student advocacy, community service',
          skills: ['Leadership', 'Event Planning', 'Communication', 'Problem Solving'],
          images: [
            require('@/assets/club/su/IMG_5240.webp'),
            require('@/assets/club/su/IMG_6197.webp'),
            require('@/assets/club/su/IMG_6266.webp'),
            require('@/assets/club/su/IMG_7365.webp')
          ],
          featured: true
        }
      ],
      selectedClub: null,
      currentImageIndex: 0
    };
  },
  methods: {
    openClub(club) {
      this.selectedClub = club;
      this.currentImageIndex = 0;
      document.body.style.overflow = 'hidden';
    },
    closeModal() {
      this.selectedClub = null;
      document.body.style.overflow = 'auto';
    },
    nextImage() {
      this.currentImageIndex = (this.currentImageIndex + 1) % this.selectedClub.images.length;
    },
    prevImage() {
      this.currentImageIndex = this.currentImageIndex === 0 ? 
        this.selectedClub.images.length - 1 : this.currentImageIndex - 1;
    }
  }
};
</script>

<style scoped>
