<template>
  <div class="art-page">
    <!-- 页面头部 -->
    <div class="page-header">
      <h1 class="page-title">My Art Portfolio</h1>
      <p class="page-subtitle">Twelve years of artistic exploration and creative expression</p>
      <div class="title-underline"></div>
    </div>

    <!-- PDF 预览已移除 -->

    <!-- 艺术作品轮播图 -->
    <div class="art-carousel">
      <h2 class="carousel-title">Art Portfolio Gallery</h2>
      <div class="carousel-container">
        <div class="carousel-wrapper">
          <div 
            v-for="(image, index) in carouselImages" 
            :key="index"
            :class="['carousel-slide', { active: currentSlide === index }]"
          >
            <img :src="image.src" :alt="image.title" class="carousel-image" />
            <div class="slide-info">
              <!-- <h3 class="slide-title">{{ image.title }}</h3>
              <p class="slide-description">{{ image.description }}</p> -->
            </div>
          </div>
        </div>
        
        <!-- 轮播控制按钮 -->
        <button class="carousel-btn prev" @click="prevSlide">‹</button>
        <button class="carousel-btn next" @click="nextSlide">›</button>
        
        <!-- 轮播指示器 -->
        <div class="carousel-indicators">
          <div 
            v-for="(image, index) in carouselImages" 
            :key="index"
            :class="['indicator', { active: currentSlide === index }]"
            @click="goToSlide(index)"
          ></div>
        </div>
      </div>
    </div>

    <!-- 艺术介绍 -->
    <div class="art-introduction">
      <div class="introduction-content">
        <p class="intro-paragraph">
          Over the past twelve years, I have immersed myself in the world of art, exploring a wide range of mediums including pencil, oil, watercolor, and egg tempera. This diverse artistic journey has resulted in twenty-seven distinctive works that span intricate landscapes, still lifes, and expressive portraits. Each piece in my portfolio stands as a testament to my dedication to craft and my enduring fascination with deeper themes such as anatomy, mortality, and culture.
        </p>
        <p class="intro-paragraph">
          My foundation was built through the patient study of still life, where I learned to articulate form, texture, and light. I began with the precision of pencil in works such as Rabbit Skeleton and Moon, refining my understanding of anatomy and tonal balance. This disciplined approach soon developed into an exploration of color and material. I experimented with the fluid transparency of watercolor, the matte opacity of gouache, the quick-drying clarity of tempera, and the textured immediacy of crayon. A piece like Piano exemplifies this phase, combining crayon and colored pencil to capture both the instrument's intricate structure and luminous sheen. These still life studies were more than exercises in representation; they became lessons in the expressive potential of materials and taught me how to use the properties of each medium to serve a creative vision.
        </p>
        <p class="intro-paragraph">
          With this technical grounding, my focus turned outward to landscapes, marking the emergence of my authentic creative voice. Here, the discipline of the hand and eye met the sensitivity of the heart. Landscapes became my primary means of expressing my relationship with the world. In Light in Forest, I used tempera to capture the tranquil play of sunlight filtering through the canopy, while Mountain conveys a sense of enduring majesty. Through these works, I discovered a natural ability to translate emotion into visual form, consciously selecting mediums and techniques to evoke feelings that range from serenity to awe. This stage taught me to project my inner perceptions onto external scenes and to transform observation into reflection.
        </p>
        <p class="intro-paragraph">
          My most recent works represent a deliberate turn inward to imagination, memory, and abstract emotion. Moving beyond literal depiction, I now create larger-scale oil paintings inspired by fairy tales and inner visions. In pieces such as Merry-go-round and Star Street, vibrant, symbolic color and dynamic composition convey feelings of nostalgia and wonder. Christmas Eve, a large oil painting, uses the rich, blendable quality of the medium to explore the somber complexity of the holiday season. This series marks a significant step forward in my practice, fully embracing the depth and luminosity of oil to construct imaginative worlds. It is my most personal body of work, expressing emotion not through realism but through the interplay of color, form, and symbol.
        </p>
        <p class="intro-paragraph">
          From a young enthusiast tracing the lines of a pencil to an artist orchestrating complex visual worlds, art has been my most constant teacher and voice. It has taught me the discipline of observation, given me a language for emotional expression through landscape, and ultimately granted me the freedom to create from pure imagination. This portfolio reflects that evolution and serves as both a record of how I perceive the world and a promise of the worlds I have yet to bring to life.
        </p>
      </div>
    </div>

    <!-- 艺术作品详情模态框 -->
    <div v-if="selectedArtwork" class="artwork-modal" @click="closeModal">
      <div class="modal-content" @click.stop>
        <button class="close-btn" @click="closeModal">×</button>
        
        <div class="modal-header">
          <h2 class="artwork-title">{{ selectedArtwork.title }}</h2>
          <div class="artwork-meta">
            <span class="medium-tag">{{ selectedArtwork.medium }}</span>
            <span class="year">{{ selectedArtwork.year }}</span>
