</template>

<script>
export default {
  name: 'Art',
  directives: {
    // v-lazy: lazy-load an <img> when it enters the viewport
    lazy: {
      inserted(el, binding) {
        // only works on <img>
        if (el.tagName !== 'IMG') return;

        // placeholder 1x1 transparent GIF to avoid broken src
        const placeholder = 'data:image/gif;base64,R0lGODlhAQABAIAAAAAAAP///ywAAAAAAQABAAACAUwAOw==';
        el.src = placeholder;

        const loadImage = () => {
          // set actual src
          el.src = binding.value;
          // when loaded, add class for fade-in
          el.addEventListener('load', () => {
            el.classList.add('loaded');
          }, { once: true });
        };

        if ('IntersectionObserver' in window) {
          const observer = new IntersectionObserver((entries, obs) => {
            entries.forEach(entry => {
              if (entry.isIntersecting) {
                loadImage();
                obs.unobserve(el);
              }
            });
          }, { rootMargin: '200px' });
          observer.observe(el);
          // keep reference for cleanup
          el._lazyObserver = observer;
        } else {
          // fallback: load immediately
          loadImage();
        }
      },
      unbind(el) {
        if (el._lazyObserver) {
          el._lazyObserver.disconnect();
          delete el._lazyObserver;
        }
      }
    }
  },
  data() {
    return {
      currentSlide: 0,
      autoPlayInterval: null,
      carouselImages: [
        {
          src: require('@/assets/art/Eva Portfolio(1)_01.webp'),
          title: 'Artwork 1',
          description: 'First piece from the portfolio series'
        },
        {
          src: require('@/assets/art/Eva Portfolio(1)_02.webp'),
          title: 'Artwork 2',
          description: 'Second piece from the portfolio series'
        },
        {
          src: require('@/assets/art/Eva Portfolio(1)_03.webp'),
          title: 'Artwork 3',
          description: 'Third piece from the portfolio series'
        },
        {
          src: require('@/assets/art/Eva Portfolio(1)_04.webp'),
          title: 'Artwork 4',
          description: 'Fourth piece from the portfolio series'
        },
        {
          src: require('@/assets/art/Eva Portfolio(1)_05.webp'),
          title: 'Artwork 5',
          description: 'Fifth piece from the portfolio series'
        },
        {
          src: require('@/assets/art/Eva Portfolio(1)_06.webp'),
          title: 'Artwork 6',
          description: 'Sixth piece from the portfolio series'
        },
        {
          src: require('@/assets/art/Eva Portfolio(1)_07.webp'),
          title: 'Artwork 7',
          description: 'Seventh piece from the portfolio series'
        },
        {
          src: require('@/assets/art/Eva Portfolio(1)_08.webp'),
          title: 'Artwork 8',
          description: 'Eighth piece from the portfolio series'
        },
        {
          src: require('@/assets/art/Eva Portfolio(1)_09.webp'),
          title: 'Artwork 9',
          description: 'Ninth piece from the portfolio series'
        },
        {
          src: require('@/assets/art/Eva Portfolio(1)_10.webp'),
          title: 'Artwork 10',
          description: 'Tenth piece from the portfolio series'
        },
        {
          src: require('@/assets/art/Eva Portfolio(1)_11.webp'),
          title: 'Artwork 11',
          description: 'Eleventh piece from the portfolio series'
        },
        {
          src: require('@/assets/art/Eva Portfolio(1)_12.webp'),
          title: 'Artwork 12',
          description: 'Twelfth piece from the portfolio series'
        },
        {
          src: require('@/assets/art/Eva Portfolio(1)_13.webp'),
          title: 'Artwork 13',
          description: 'Thirteenth piece from the portfolio series'
        },
        {
          src: require('@/assets/art/Eva Portfolio(1)_14.webp'),
          title: 'Artwork 14',
          description: 'Fourteenth piece from the portfolio series'
        },
        {
          src: require('@/assets/art/Eva Portfolio(1)_15.webp'),
          title: 'Artwork 15',
          description: 'Fifteenth piece from the portfolio series'
        },
        {
          src: require('@/assets/art/Eva Portfolio(1)_16.webp'),
          title: 'Artwork 16',
          description: 'Sixteenth piece from the portfolio series'
        },
        {
          src: require('@/assets/art/Eva Portfolio(1)_17.webp'),
          title: 'Artwork 17',
          description: 'Seventeenth piece from the portfolio series'
        },
        {
          src: require('@/assets/art/Eva Portfolio(1)_18.webp'),
          title: 'Artwork 18',
          description: 'Eighteenth piece from the portfolio series'
        },
        {
          src: require('@/assets/art/Eva Portfolio(1)_19.webp'),
          title: 'Artwork 19',
          description: 'Nineteenth piece from the portfolio series'
        }
      ],
      selectedArtwork: null
    };
  },
  
  mounted() {
    this.startAutoPlay();
  },
  
  beforeUnmount() {
    this.stopAutoPlay();
  },
  
  methods: {
    startAutoPlay() {
      this.autoPlayInterval = setInterval(() => {
        this.nextSlide();
      }, 5000); // 每5秒自动切换
    },
    
    stopAutoPlay() {
      if (this.autoPlayInterval) {
        clearInterval(this.autoPlayInterval);
        this.autoPlayInterval = null;
      }
    },
    
    nextSlide() {
      this.currentSlide = (this.currentSlide + 1) % this.carouselImages.length;
    },
    
    prevSlide() {
      this.currentSlide = this.currentSlide === 0 ? this.carouselImages.length - 1 : this.currentSlide - 1;
    },
    
    goToSlide(index) {
      this.currentSlide = index;
    },
    
    openArtwork(artwork) {
      this.selectedArtwork = artwork;
    },
    
    closeModal() {
      this.selectedArtwork = null;
      document.body.style.overflow = 'auto';
    }
  }
};
</script>

