<template>
  <div class="home-container">
    <!-- 加载跳动点阵 -->
    <div v-if="!allImagesLoaded" class="loading-overlay" :class="{ 'fade-out': isFadingOut }">
      <div class="loading-content">
        <div class="dot-container">
          <div class="dot"></div>
          <div class="dot"></div>
          <div class="dot"></div>
          <div class="dot"></div>
          <div class="dot"></div>
        </div>
        <div class="loading-text">Loading...</div>
      </div>
    </div>
    
    <!-- 主界面内容 -->
    <div class="main-content">
      <!-- 照片墙背景 - 在loading阶段就逐步显示已加载的图片 -->
      <div class="photo-wall">
        <!-- 幕布效果 -->
        <div class="photo-wall-overlay"></div>
        <div 
          v-for="(photo, index) in photos" 
          :key="index" 
          class="photo-item"
          :style="{
            top: photo.top + '%',
            left: photo.left + '%',
            width: photo.width,
            height: photo.height,
            opacity: photo.loaded && photo.visible ? photo.opacity : 0,
            transform: `rotate(${photo.rotate}deg)`,
            animationDelay: photo.delay + 's',
            transition: 'all 0.5s ease-in-out'
          }"
        >
          <img v-if="photo.loaded" :src="photo.url" alt="Photo" />
        </div>
      </div>
      
      <!-- 中央菜单 -->
      <div class="center-menu">
        <div class="menu-items">
          <router-link to="/about" class="menu-item">About</router-link>
          <span class="menu-separator">/</span>
          <router-link to="/projects" class="menu-item">Projects</router-link>
          <span class="menu-separator">/</span>
          <router-link to="/research" class="menu-item">Research</router-link>
          <span class="menu-separator">/</span>
          <router-link to="/club" class="menu-item">Club</router-link>
          <span class="menu-separator">/</span>
          <router-link to="/art" class="menu-item">Art</router-link>
          <span class="menu-separator">/</span>
          <router-link to="/contact" class="menu-item">Contact</router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { stopConfetti } from '../utils/confettiEffect.js';

export default {
  data() {
    return {
      displayedText: '',
      fullText: 'EVA LU',
      typeSpeed: 150,
      eraseSpeed: 100,
      pauseTime: 2000,
      isTyping: true,
      photos: [],
      allImagesLoaded: false,
      loadingProgress: 0,
      loadedImagesCount: 0,
      totalImagesCount: 0,
      minLoadingTime: 3000, // 3秒最小加载时间
      loadingStartTime: null,
      loadingTimer: null,
      isFadingOut: false
    };
  },
  mounted() {
    this.loadingStartTime = Date.now();
    
    // 在loading阶段禁用彩纸特效，减少性能压力
    stopConfetti();
    
    // 先初始化照片墙结构，但不立即加载图片
    this.initPhotoWallStructure();
    
    // 启动后台逐步加载图片
    this.startBackgroundImageLoading();
    
    // 启动最小加载时间计时器
    this.loadingTimer = setTimeout(() => {
      this.checkAllImagesLoaded();
    }, this.minLoadingTime);
  },
  methods: {
    initPhotoWallStructure() {
      // 只创建照片墙的结构，不立即加载图片
      const photos = [];
      
      // 根据屏幕尺寸动态调整行列数，确保图片铺满屏幕
      const screenWidth = window.innerWidth;
      const screenHeight = window.innerHeight;
      
      // 计算合适的行列数，确保图片不会太小
      const baseCols = screenWidth > 768 ? 8 : 6; // 大屏幕8列，小屏幕6列
      const baseRows = screenHeight > 600 ? 5 : 4; // 高屏幕5行，矮屏幕4行
      
      // 根据屏幕比例微调行列数
      const aspectRatio = screenWidth / screenHeight;
      let cols = baseCols;
      let rows = baseRows;
      
      if (aspectRatio > 1.5) {
        // 宽屏，增加列数
        cols = Math.min(baseCols + 2, 10);
      } else if (aspectRatio < 0.8) {
        // 窄屏，增加行数
        rows = Math.min(baseRows + 1, 6);
      }
      
      const totalPhotos = rows * cols;
      
      // 设置总图片数量
      this.totalImagesCount = totalPhotos;
      
      // 计算每个格子的宽度和高度百分比
      const cellWidth = 100 / cols;
      const cellHeight = 100 / rows;
      
      for (let row = 0; row < rows; row++) {
        for (let col = 0; col < cols; col++) {
          // 精确的表格布局，每个格子铺满
          const top = row * cellHeight;
          const left = col * cellWidth;
          
          photos.push({
            url: '', // 初始为空，后台加载时再设置
            top: top,
            left: left,
            width: cellWidth + '%', // 宽度铺满整个格子
            height: cellHeight + '%', // 高度铺满整个格子
            opacity: 1, // 固定不透明度
            rotate: 0, // 去除旋转角度
            delay: Math.random() * 5, // 0-5秒延迟
            visible: true, // 初始可见
            loaded: false // 标记是否已加载
          });
        }
      }
      this.photos = photos;
    },
    
