        require('@/assets/art/Eva Portfolio(1)_16.webp'),
        require('@/assets/art/Eva Portfolio(1)_17.webp'),
        require('@/assets/art/Eva Portfolio(1)_18.webp'),
        require('@/assets/art/Eva Portfolio(1)_19.webp'),
        
        // club/bc目录图片
        require('@/assets/club/bc/3f81876bba27a94fa7b999fcb41331b9.webp'),
        require('@/assets/club/bc/5362fe495b2d6c6b399198a12750c105.webp'),
        require('@/assets/club/bc/7bb8a701446f0358734f1391c5c11746.webp'),
        require('@/assets/club/bc/8ba38f461d52b3873e607a682ea6a7ba.webp'),
        require('@/assets/club/bc/cf1863aaedb12d6dfad4a60dfe23185c.webp'),
        require('@/assets/club/bc/DSC02670.webp'),
        require('@/assets/club/bc/DSC02674.webp'),
        require('@/assets/club/bc/DSC02687.webp'),
        require('@/assets/club/bc/DSC02690.webp'),
        require('@/assets/club/bc/DSC02691.webp'),
        require('@/assets/club/bc/DSC02695.webp'),
        require('@/assets/club/bc/DSC02709.webp'),
        require('@/assets/club/bc/DSC02712.webp'),
        require('@/assets/club/bc/DSC02714.webp'),
        require('@/assets/club/bc/DSC02748.webp'),
        require('@/assets/club/bc/DSC02754.webp'),
        require('@/assets/club/bc/IMG_0122.webp'),
        require('@/assets/club/bc/IMG_0420.webp'),
        require('@/assets/club/bc/IMG_0429.webp'),
        require('@/assets/club/bc/IMG_7279.webp'),
        require('@/assets/club/bc/IMG_7280.webp'),
        require('@/assets/club/bc/IMG_7281.webp'),
        require('@/assets/club/bc/IMG_7284.webp'),
        require('@/assets/club/bc/IMG_8190.webp'),
        
        // club/su目录图片
        require('@/assets/club/su/IMG_5240.webp'),
        require('@/assets/club/su/IMG_6197.webp'),
        require('@/assets/club/su/IMG_6266.webp'),
        require('@/assets/club/su/IMG_7365.webp'),
        
        // projects目录精选图片
        require('@/assets/projects/Campus Publicity & Art/10A2罗一然4.9 2023-03-03 18.24.12.webp'),
        require('@/assets/projects/Campus Publicity & Art/IMG_5643.webp'),
        require('@/assets/projects/Campus Publicity & Art/IMG_6025.webp'),
        require('@/assets/projects/Campus Publicity & Art/IMG_7364.webp'),
        require('@/assets/projects/Campus Publicity & Art/IMG_8282.webp'),
        require('@/assets/projects/Campus Publicity & Art/IMG_9134.webp'),
