        require('@/assets/projects/HiMCM/IMG_8237.webp'),
        require('@/assets/projects/IMMC/3be683d85e352e33bde75a5ddd70afde.webp'),
        require('@/assets/projects/IMMC/IMG_2877.webp'),
        require('@/assets/projects/IMMC/IMG_3782.webp'),
        require('@/assets/projects/IMMC/IMG_5598.webp'),
        require('@/assets/projects/NextGen/WechatIMG11.webp'),
        require('@/assets/projects/NextGen/WechatIMG12.webp'),
        require('@/assets/projects/NextGen/WechatIMG13.webp'),
        require('@/assets/projects/NextGen/WechatIMG14.webp'),
        require('@/assets/projects/No. 80 Green Garden/25a08d56d56453a5098f76199e7be792.webp'),
        require('@/assets/projects/No. 80 Green Garden/IMG_0458.webp'),
        require('@/assets/projects/No. 80 Green Garden/IMG_8857.webp'),
        require('@/assets/projects/PBL Targeted Lung Cancer Treatment/59a747dd43d998ca3c464db4d093dfd1.webp'),
        require('@/assets/projects/PBL Targeted Lung Cancer Treatment/IMG_4332.webp'),
        require('@/assets/projects/PBL Targeted Lung Cancer Treatment/IMG_4564.webp'),
        require('@/assets/projects/Renewable Ion-Exchange Power System (SuperSMEst)/57a977f672c5b0c650360c4bbde4d5dd.webp'),
        
        // about图片
        require('@/assets/about/WechatIMG27.webp'),
        
        // contact图片
        require('@/assets/contact/contact.webp')
      ];
      
      // 分批预加载，避免一次性加载过多图片导致卡顿
      const batchSize = 10;
      let currentBatch = 0;
      
      const loadBatch = () => {
        const start = currentBatch * batchSize;
        const end = Math.min(start + batchSize, photoUrls.length);
        
        for (let i = start; i < end; i++) {
          const img = new Image();
          img.src = photoUrls[i];
        }
        
        currentBatch++;
        if (currentBatch * batchSize < photoUrls.length) {
          // 延迟加载下一批，避免阻塞主线程
          setTimeout(loadBatch, 100);
        }
      };
      
      loadBatch();
    }
