};
</script>

<style scoped>
.home-container {
  height: 100vh;
  position: relative;
  overflow: hidden;
  background: #111;
  margin-top: -80px;
  padding-top: 80px;
}

/* 加载覆盖层样式 */
.loading-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #111;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
  transition: opacity 0.8s ease-in-out, transform 0.8s ease-in-out;
}

.loading-overlay.fade-out {
  opacity: 0;
  transform: scale(0.9);
}

.loading-content {
  text-align: center;
  color: #fff;
}

.dot-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 40px;
}

.dot {
  width: 20px;
  height: 20px;
  margin: 0 8px;
  border-radius: 50%;
  animation: bounce 1.5s infinite ease-in-out;
}

.dot:nth-child(1) {
  background-color: #ff6b6b;
  box-shadow: 0 0 10px #ff6b6b;
}

.dot:nth-child(2) {
  background-color: #ff9ff3;
  animation-delay: 0.2s;
  box-shadow: 0 0 10px #ff9ff3;
}

.dot:nth-child(3) {
  background-color: #54a0ff;
  animation-delay: 0.4s;
  box-shadow: 0 0 10px #54a0ff;
}

.dot:nth-child(4) {
  background-color: #5f27cd;
  animation-delay: 0.6s;
  box-shadow: 0 0 10px #5f27cd;
}

.dot:nth-child(5) {
  background-color: #4ecdc4;
  animation-delay: 0.8s;
  box-shadow: 0 0 10px #4ecdc4;
}

@keyframes bounce {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-20px);
  }
}

.loading-text {
  font-size: 1.5rem;
  color: rgba(255, 255, 255, 0.9);
  font-family: 'Helvetica Neue', Arial, sans-serif;
  text-transform: uppercase;
  letter-spacing: 3px;
  animation: textGlow 2s ease-in-out infinite;
