<template>
  <div class="research-page">
    <!-- 研究详情模态框 -->
    <div v-if="selectedResearch" class="research-modal" @click="closeModal">
      <div class="modal-content" @click.stop>
        <button class="close-btn" @click="closeModal">×</button>
        
        <div class="modal-header">
          <h2 class="research-title">{{ selectedResearch.name }}</h2>
          <div class="research-meta">
            <span class="category-tag">{{ selectedResearch.category }}</span>
            <span class="year">{{ selectedResearch.year }}</span>
          </div>
        </div>
        
        <div class="modal-body">
          <!-- 图片轮播 -->
          <div class="image-carousel">
            <div class="carousel-container">
              <img 
                :src="selectedResearch.images[currentImageIndex]" 
                :alt="selectedResearch.name"
                class="carousel-image"
              />
              <button v-if="selectedResearch.images.length > 1" class="carousel-btn prev" @click="prevImage">‹</button>
              <button v-if="selectedResearch.images.length > 1" class="carousel-btn next" @click="nextImage">›</button>
              <div v-if="selectedResearch.images.length > 1" class="carousel-indicators">
                <span 
                  v-for="(img, index) in selectedResearch.images" 
                  :key="index"
                  :class="{ active: index === currentImageIndex }"
                  @click="currentImageIndex = index"
                ></span>
              </div>
            </div>
          </div>
          
          <!-- 研究详情 -->
          <div class="research-details">
            <div class="detail-section">
              <h3>Research Overview</h3>
              <p class="animated-text">{{ selectedResearch.description }}</p>
            </div>
            
            <div class="detail-grid">
              <div class="detail-item">
                <h4>Methodology</h4>
                <p>{{ selectedResearch.methodology }}</p>
              </div>
              <div class="detail-item">
                <h4>Duration</h4>
                <p>{{ selectedResearch.duration }}</p>
              </div>
              <!-- Technologies removed from research details UI per request -->
            </div>
          </div>
        </div>
      </div>
    </div>
    
    <!-- 主研究列表 -->
    <div class="research-container">
      <div class="page-header">
        <h1 class="page-title">My Research</h1>
        <p class="page-subtitle">Academic research and scientific investigations</p>
        <div class="title-underline"></div>
      </div>
      
      <!-- 研究网格 -->
      <div class="research-grid">
        <div 
          v-for="research in researches" 
          :key="research.id"
          class="research-card"
          @click="openResearch(research)"
        >
          <div class="card-image">
            <img :src="research.images[0]" :alt="research.name" />
            <div v-if="research.featured" class="featured-badge">Featured</div>
            <div class="card-overlay">
              <span class="view-details">View Details</span>
            </div>
          </div>
          <div class="card-content">
            <h3 class="card-title">{{ research.shortName }}</h3>
            <p class="card-description">{{ research.description.substring(0, 120) }}...</p>
            <div class="card-meta">
              <span class="card-category">{{ research.category }}</span>
              <span class="card-year">{{ research.year }}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getAllResearches } from '../data/research.js';

export default {
  name: 'Research',
  data() {
    return {
      researches: [],
      selectedResearch: null,
      currentImageIndex: 0
    };
  },
  mounted() {
    this.researches = getAllResearches();
  },
  methods: {
    openResearch(research) {
      this.selectedResearch = research;
      this.currentImageIndex = 0;
      document.body.style.overflow = 'hidden';
    },
    closeModal() {
      this.selectedResearch = null;
      document.body.style.overflow = 'auto';
    },
    nextImage() {
      this.currentImageIndex = (this.currentImageIndex + 1) % this.selectedResearch.images.length;
    },
    prevImage() {
      this.currentImageIndex = this.currentImageIndex === 0 ? 
        this.selectedResearch.images.length - 1 : this.currentImageIndex - 1;
    }
  }
};
</script>

<style scoped>
.research-page {
  background: #111;
  min-height: 100vh;
  padding: 60px 0 80px 0;
}

/* 页面头部 */
.page-header {
  text-align: center;
  margin-bottom: 60px;
  padding: 0 20px;
}

.page-title {
  font-size: 3.5rem;
  font-weight: 700;
  color: #fff;
  margin-bottom: 16px;
  letter-spacing: 2px;
  font-family: 'Georgia', 'Times New Roman', serif;
  background: linear-gradient(45deg, #ff6b6b, #54a0ff, #5f27cd);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: titleGlow 3s ease-in-out infinite;
}

@keyframes titleGlow {
  0%, 100% { text-shadow: 0 0 20px rgba(255, 107, 107, 0.5); }
  50% { text-shadow: 0 0 30px rgba(84, 160, 255, 0.8); }
}

.page-subtitle {
  font-size: 1.2rem;
  color: rgba(255, 255, 255, 0.8);
  margin-bottom: 24px;
  font-weight: 300;
  letter-spacing: 1px;
}

.title-underline {
  width: 100px;
  height: 4px;
  background: linear-gradient(90deg, #ff6b6b, #54a0ff);
  border-radius: 2px;
  margin: 0 auto;
  animation: underlinePulse 2s ease-in-out infinite;
}

@keyframes underlinePulse {
  0%, 100% { transform: scaleX(1); opacity: 1; }
  50% { transform: scaleX(1.1); opacity: 0.8; }
}

/* 研究网格 */
.research-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  gap: 30px;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

/* 研究卡片 */
.research-card {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 16px;
  overflow: hidden;
  cursor: pointer;
  transition: all 0.4s ease;
  border: 1px solid rgba(255, 255, 255, 0.1);
  position: relative;
}

.research-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.4);
  border-color: rgba(255, 107, 107, 0.3);
}

.research-card:hover .card-overlay {
  opacity: 1;
}

.card-image {
  position: relative;
  height: 250px;
  overflow: hidden;
}

.card-image img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  transition: transform 0.4s ease;
  background: rgba(255, 255, 255, 0.05);
}

.research-card:hover .card-image img {
  transform: scale(1.1);
}

.featured-badge {
  position: absolute;
  top: 16px;
  right: 16px;
  background: linear-gradient(45deg, #ff6b6b, #ff9ff3);
  color: #fff;
  padding: 6px 12px;
  border-radius: 12px;
  font-size: 0.8rem;
  font-weight: 600;
  z-index: 2;
}

.card-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.view-details {
  color: #fff;
  font-size: 1.2rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.card-content {
  padding: 24px;
}

.card-title {
  font-size: 1.4rem;
  font-weight: 600;
  color: #fff;
  margin-bottom: 12px;
  line-height: 1.3;
}

.card-description {
  color: rgba(255, 255, 255, 0.7);
  line-height: 1.5;
  margin-bottom: 16px;
  font-size: 0.95rem;
}

.card-meta {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.card-category {
  background: rgba(84, 160, 255, 0.2);
  color: #54a0ff;
  padding: 4px 12px;
  border-radius: 12px;
  font-size: 0.8rem;
  font-weight: 500;
}

.card-year {
  color: rgba(255, 255, 255, 0.6);
  font-size: 0.9rem;
}

/* 模态框样式 */
.research-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.9);
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 40px;
  animation: modalFadeIn 0.3s ease;
}

@keyframes modalFadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

.modal-content {
  background: #1a1a1a;
  border-radius: 20px;
  max-width: 900px;
  width: 100%;
  max-height: 90vh;
  overflow-y: auto;
  position: relative;
  border: 1px solid rgba(255, 255, 255, 0.1);
  animation: modalSlideIn 0.3s ease;
  scrollbar-width: none; /* Firefox */
  -ms-overflow-style: none; /* IE and Edge */
}

.modal-content::-webkit-scrollbar {
  display: none; /* Chrome, Safari and Opera */
}

@keyframes modalSlideIn {
  from { transform: scale(0.9); opacity: 0; }
  to { transform: scale(1); opacity: 1; }
}

.close-btn {
  position: absolute;
  top: 20px;
  right: 20px;
  background: rgba(255, 255, 255, 0.1);
  border: none;
  color: #fff;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  font-size: 1.5rem;
  cursor: pointer;
  z-index: 10;
  transition: all 0.3s ease;
}

.close-btn:hover {
  background: rgba(255, 107, 107, 0.8);
  transform: rotate(90deg);
}

.modal-header {
  padding: 40px 40px 20px 40px;
}

.research-title {
  font-size: 1.8rem;
  font-weight: 600;
  color: #fff;
  margin-bottom: 16px;
  line-height: 1.4;
  word-wrap: break-word;
}

.research-meta {
  display: flex;
  gap: 16px;
  align-items: center;
}

.category-tag {
  background: linear-gradient(45deg, #ff6b6b, #54a0ff);
  color: #fff;
  padding: 6px 16px;
  border-radius: 20px;
  font-size: 0.9rem;
  font-weight: 600;
}

.year {
  color: rgba(255, 255, 255, 0.7);
  font-size: 1rem;
}

.modal-body {
  padding: 40px;
}

/* 图片轮播 */
.image-carousel {
  margin-bottom: 40px;
}

.carousel-container {
  position: relative;
  border-radius: 12px;
  overflow: hidden;
  background: #2a2a2a;
}

.carousel-image {
  width: 100%;
  max-height: 500px;
  object-fit: contain;
  display: block;
  background: #2a2a2a;
}

.carousel-btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: rgba(0, 0, 0, 0.7);
  border: none;
  color: #fff;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  font-size: 1.5rem;
  cursor: pointer;
  transition: all 0.3s ease;
}

.carousel-btn:hover {
  background: rgba(255, 107, 107, 0.8);
}

.carousel-btn.prev {
  left: 20px;
}

.carousel-btn.next {
  right: 20px;
}

.carousel-indicators {
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  gap: 8px;
}

.carousel-indicators span {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.3);
  cursor: pointer;
  transition: all 0.3s ease;
}

.carousel-indicators span.active {
  background: #ff6b6b;
  transform: scale(1.2);
}

/* 研究详情 */
.research-details h3 {
  color: #fff;
  font-size: 1.5rem;
  margin-bottom: 20px;
  font-weight: 600;
}

.animated-text {
  color: rgba(255, 255, 255, 0.9);
  line-height: 1.7;
  font-size: 1.1rem;
  animation: textReveal 1s ease-out;
}

@keyframes textReveal {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}

.detail-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 30px;
  margin-top: 40px;
}

.detail-item h4 {
  color: #54a0ff;
  font-size: 1rem;
  margin-bottom: 8px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.detail-item p {
  color: rgba(255, 255, 255, 0.8);
  line-height: 1.6;
  font-size: 0.95rem;
}

.tech-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 6px;
}

.tech-tag {
  background: rgba(255, 107, 107, 0.15);
  color: #ff6b6b;
  padding: 4px 10px;
  border-radius: 8px;
  font-size: 0.8rem;
  font-weight: 500;
  border: 1px solid rgba(255, 107, 107, 0.3);
}

/* 响应式设计 */
@media (max-width: 768px) {
  .research-page {
    padding: 40px 0 60px 0;
  }
  
  .page-title {
    font-size: 2.5rem;
  }
  
  .research-grid {
    grid-template-columns: 1fr;
    gap: 20px;
    padding: 0 15px;
  }
  
  .research-modal {
    padding: 20px;
  }
  
  .modal-content {
    max-height: 95vh;
  }
  
  .modal-header,
  .modal-body {
    padding: 30px 20px;
  }
  
  .research-title {
    font-size: 1.5rem;
    line-height: 1.3;
  }
  
  .carousel-image {
    height: 250px;
  }
  
  .detail-grid {
    grid-template-columns: 1fr;
    gap: 20px;
  }
}

@media (max-width: 480px) {
  .page-title {
    font-size: 2rem;
  }
  
  .research-grid {
    padding: 0 10px;
  }
  
  .research-card {
    margin: 0 5px;
  }
}
</style>
