    description: 'As the lead researcher of the Environmental Impact Evaluation Framework (EIEF) for High-Performance Computing (HPC), I directed a comprehensive study designed to quantify and mitigate the environmental footprint of large-scale computing systems. This project arose from the growing global concern about the rapid expansion of HPC infrastructure and its soaring energy demands, which are projected to constitute a substantiTo achieve this goal, I conceptualized and developed three interconnected mathematical models, each addressing a distinct aspect of HPC’s environmental impact. The first, the Environmental Impact Evaluation Model (EIEM), quantified the direct carbon emissions generated by HPC energy consumption. I began by compiling large-scale datasets from the TOP500 HPC list, global energy statistics, and regional electricity carbon intensities. Through regression and sensitivity analyses, the model projected potential effects of HPC emissions on temperature rise, sea level increase, and ocean acidification under different growth and energy scenarios. The results revealed a concerning trend: if current energy consumption patterns persist, HPC could contribute nearly 20 percent of the world’s total environmental burden by 2030. This model provided the foundation for the entire framework and highlighted the urgency of the issue.The second model, the Carbon Intensity Approximation Model (CIAM), examined the potential of renewable energy integration to mitigate emissions. By simulating various levels of renewable adoption within global data centers, CIAM quantified the reduction in carbon output under different grid configurations. I designed this model to capture the complex relationships between renewable energy input, carbon efficiency, and operational performance. Our analysis showed that even a 30 percent increase in renewable energy usage could reduce overall HPC emissions by nearly half, emphasizing the crucial role of sustainable energy transitions in the future of computing technology.The third model, the E-Waste Assessment Model (EWAM), focused on the long-term ecological consequences of HPC hardware disposal. Using the entropy-weight method, I evaluated the relative toxicity of heavy metals and rare earth elements in HPC components and their combined impact on soil, air, and water systems. The model compared several disposal scenarios, ranging from unregulated dumping to closed-loop recycling, to estimate environmental and health risks. The findings indicated that improper disposal of e-waste could cause severe heavy metal contamination, while improved recycling systems could reduce environmental toxicity by more than 60 percent.Beyond model development, I consolidated our findings into a policy brief for the United Nations Advisory Board, advocating for the inclusion of a specific sustainability target for HPC within the UN 2030 Development Goals. I wrote the section recommending strategies to reduce non-renewable energy dependency, implement carbon-aware scheduling in HPC facilities, and improve global standards for e-waste management and recycling. The brief combined rigorous modeling results with policy-oriented proposals, demonstrating how quantitative analysis can inform effective environmental governance.Leading this research strengthened my ability to connect scientific precision with practical application. It required balancing analytical rigor with real-world feasibility and coordinating a multidisciplinary team that included experts in data science, environmental studies, and engineering. The EIEF project ultimately became an example of how mathematics can serve as a bridge between technological progress and environmental sustainability.',
