<template>
  <div id="app">
    <!-- 只在非首页显示导航栏 -->
    <Navbar v-if="!isHomePage" />
    <div :class="['router-view-container', { 'no-navbar': isHomePage }]">
      <router-view @imagesLoaded="handleImagesLoaded" />
    </div>
  </div>
</template>

<script>
import Navbar from './components/Navbar.vue';
import { startConfetti, stopConfetti } from './utils/confettiEffect.js';

export default {
  components: { Navbar },
  data() {
    return {
      imagesLoaded: false // 保留图片加载状态用于特效控制
    };
  },
  
  computed: {
    isHomePage() {
      // 判断当前路由是否为首页
      return this.$route.path === '/' || this.$route.path === '/home';
    }
  },
  
  mounted() {
    // 监听页面可见性变化
    document.addEventListener('visibilitychange', this.handleVisibilityChange);
    
    // 监听路由变化，确保每次路由切换都滚动到顶部
    this.$router.afterEach(() => {
      this.scrollToTop();
    });
    
    // 不在页面加载时立即启动彩纸特效，等待图片加载完成
    // 特效将在handleImagesLoaded方法中启动
  },
  
  beforeUnmount() {
    // 组件销毁前停止特效
    this.stopConfettiEffect();
    document.removeEventListener('visibilitychange', this.handleVisibilityChange);
  },
  
  methods: {
    startConfettiEffect() {
      startConfetti();
    },
    
    stopConfettiEffect() {
      stopConfetti();
    },
    
    handleVisibilityChange() {
      if (document.hidden) {
        this.stopConfettiEffect();
      } else {
        // 只有在图片加载完成后才重新启动特效
        if (this.imagesLoaded) {
          this.startConfettiEffect();
        }
      }
    },
    
    handleImagesLoaded() {
      // 图片加载完成 — 保持彩纸特效默认关闭
      // 若需要手动启用，可在控制处调用 startConfetti()/enableMouseTrail()
      this.imagesLoaded = true;
    },
    
    scrollToTop() {
      // 使用多种方法确保滚动到顶部
      if (typeof window !== 'undefined') {
        // 方法1: 立即滚动
        window.scrollTo(0, 0);
        
        // 方法2: 使用requestAnimationFrame确保在下一帧执行
        requestAnimationFrame(() => {
          window.scrollTo(0, 0);
        });
        
        // 方法3: 延迟执行确保DOM已更新
        setTimeout(() => {
          window.scrollTo(0, 0);
          
          // 方法4: 使用scrollIntoView
          const appElement = document.getElementById('app');
          if (appElement) {
            appElement.scrollIntoView({ behavior: 'instant' });
          }
          
          // 方法5: 直接设置scrollTop
          if (document.documentElement) {
            document.documentElement.scrollTop = 0;
          }
          if (document.body) {
            document.body.scrollTop = 0;
          }
        }, 100);
      }
    }
  }
};
</script>

<style>
/*
  全局隐藏滚动条但保留滚动：
  - Firefox: scrollbar-width: none;
  - IE/Edge (旧版): -ms-overflow-style: none;
  - WebKit (Chrome/Safari/Opera): 把 scrollbar 宽度/高度设为 0
  将样式应用到 html、body 和 #app，覆盖页面级滚动条。
*/
html, body, #app {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}

/* WebKit browsers (Chrome, Safari, newer Edge Chromium, Opera) */
html::-webkit-scrollbar,
body::-webkit-scrollbar,
#app::-webkit-scrollbar {
  width: 0;
  height: 0;
}

body {
  margin: 0;
  background: #111;
  color: #fff;
  font-family: 'Helvetica Neue', Arial, sans-serif;
  font-size: 1.2em;
  overflow-x: hidden;
  cursor: default;
}

html, body {
  height: 100%;
  overflow-x: hidden;
}

#app {
  min-height: 100vh;
}

.router-view-container {
  padding-top: 80px;
  min-height: calc(100vh - 80px);
}

.router-view-container.no-navbar {
  padding-top: 0;
  min-height: 100vh;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Helvetica Neue', Arial, sans-serif;
  font-weight: bold;
  font-size: 2.2em;
}

p, div, span, input, textarea, button {
  font-family: 'Helvetica Neue', Arial, sans-serif;
  font-size: 1.2em;
}

/* 修复Element UI v-modal遮罩层的z-index问题 */
.v-modal {
  z-index: 1 !important;
}

/* 链接和按钮上的鼠标样式 */
a, button, .el-menu-item {
  cursor: pointer;
}
</style>
