<template>
  <div>
    <div v-if="!isMobile" class="navbar-row">
      <div class="navbar-title">EVA</div>
      <el-menu
        :default-active="active"
        class="navbar"
        mode="horizontal"
        background-color="#111"
        text-color="#fff"
        active-text-color="#ffd04b"
      >
        <el-menu-item v-for="item in menu" :key="item.path" :index="item.path" @click="go(item.path)">{{ item.name }}</el-menu-item>
      </el-menu>
    </div>
    <div v-else class="mobile-navbar">
      <span class="navbar-title-mobile">ZIYING LV</span>
      <button class="menu-btn" @click="drawer = true">
        <i class="el-icon-menu"></i>
      </button>
      <el-drawer :visible.sync="drawer" direction="rtl" size="200px" :with-header="false" custom-class="dark-drawer">
        <el-menu
          :default-active="active"
          class="vertical-navbar"
          background-color="#111"
          text-color="#fff"
          active-text-color="#ffd04b"
          mode="vertical"
        >
          <el-menu-item v-for="item in menu" :key="item.path" :index="item.path" @click="go(item.path); drawer = false">{{ item.name }}</el-menu-item>
        </el-menu>
      </el-drawer>
    </div>
    
  </div>
</template>

<script>
export default {
  data() {
    return {
      drawer: false,
      menu: [
        { name: 'Home', path: '/home' },
        { name: 'About', path: '/about' },
        { name: 'Projects', path: '/projects' },
        { name: 'Research', path: '/research' },
        { name: 'Club', path: '/club' },
        { name: 'Art', path: '/art' },
        { name: 'Contact', path: '/contact' }
      ],
      active: this.$route.path,
      isMobile: false
    };
  },
  watch: {
    '$route.path'(val) {
      this.active = val;
    }
  },
  mounted() {
    this.checkMobile();
    window.addEventListener('resize', this.checkMobile);
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.checkMobile);
  },
  methods: {
    go(path) {
      if (this.$route.path !== path) {
        this.$router.push(path);
      }
    },
    checkMobile() {
      this.isMobile = window.innerWidth < 1100;
    }
  }
};
</script>

