// 研究项目数据配置
export const researches = [
  {
    id: 'pv-africa',
    name: 'Climate Change Impact on Photovoltaic Potential in Africa',
    shortName: 'PV Potential in Africa',
    category: 'Climate Research',
    year: '2024',
    images: [
      require('@/assets/research/Climate Change Impact on Photovoltaic Potential in Africa/Research Flowchart.webp'),
      require('@/assets/research/Climate Change Impact on Photovoltaic Potential in Africa/Screenshot 2025-10-23 at 21.28.02.webp'),
      require('@/assets/research/Climate Change Impact on Photovoltaic Potential in Africa/Screenshot 2025-10-23 at 21.29.11.webp'),
      require('@/assets/research/Climate Change Impact on Photovoltaic Potential in Africa/Screenshot 2025-10-23 at 21.29.50.webp'),
      require('@/assets/research/Climate Change Impact on Photovoltaic Potential in Africa/Screenshot 2025-10-23 at 21.30.09.webp'),
      require('@/assets/research/Climate Change Impact on Photovoltaic Potential in Africa/Screenshot 2025-10-23 at 21.30.38.webp')
    ],
    description: 'In this research project, I examined how climate change has affected the photovoltaic (PV) energy potential across Africa over the past four decades. The study was motivated by a striking contradiction: although Africa receives the most sunlight in the world, it accounts for less than two percent of global installed solar capacity. I wanted to understand how changing climatic conditions might influence the continent’s true potential for solar energy generation. Using high-resolution ERA5 reanalysis data from 1980 to 2020, I analyzed three key climate variables: solar radiation, air temperature, and wind speed. I constructed a multivariate decomposition framework to determine how each factor contributed to variations in PV potential and applied stability analysis through the coefficient of variation to assess annual reliability. This approach allowed me to evaluate both long-term trends and regional differences in solar energy potential. The results revealed that Africa’s overall PV potential increased by about 3.2 percent due to rising solar radiation, although higher air temperatures slightly reduced conversion efficiency. East Africa showed the greatest improvement, with over six percent growth and high stability, making it the most favorable region for large-scale solar development. North Africa’s potential declined slightly because of temperature-induced efficiency losses, while southern regions displayed greater variability caused by heatwaves and dust events. This research emphasized that the key to sustainable solar expansion in Africa lies not only in resource abundance but also in long-term stability and resilience. The project strengthened my skills in data modeling, statistical analysis, and climate interpretation, and reinforced my commitment to applying research for sustainable and equitable energy development.',
    methodology: 'Multivariate decomposition framework using ERA5 reanalysis data (1980-2020), stability analysis through coefficient of variation, regional trend assessment',
    technologies: ['Climate Data Analysis', 'Statistical Modeling', 'ERA5 Reanalysis', 'Geospatial Analysis'],
    duration: 'Hours/Week:6 Weeks/Year:30',
    featured: true
  },
  {
    id: 'sawh-middle-east',
    name: 'Quantifying the Irrigation Support Potential of Solar Atmospheric Water Harvesting in Arid Regions - A Case Study in the Middle East',
    shortName: 'SAWH Irrigation Support',
    category: 'Water Resources',
    year: '2025',
    images: [
      require('@/assets/research/Quantifying the Irrigation Support Potential of Solar Atmospheric Water Harvesting in Arid Regions-  A Case Study in the Middle East/wKj0iWGoltiAFbipAAnTg8VWDp0823.webp')
    ],
    description: 'I investigated the potential of solar-powered atmospheric water harvesting (SAWH) as a sustainable irrigation solution in the Middle East, a region where agriculture faces severe water scarcity and relies heavily on groundwater. The project aimed to determine whether SAWH could supplement irrigation and reduce water stress for key crops such as rice, wheat, and date palm. Using climate data from ERA5-Land (1980–2020), I calculated crop water demand through reference evapotranspiration (ET₀) and standard crop coefficients (Kc). I then developed a ratio model (R) to compare water yield from SAWH with irrigation requirements, enabling spatial and seasonal assessment across multiple countries. The analysis revealed significant regional variation. In some areas, atmospheric water could exceed local irrigation needs (R up to 50:1), while in highly arid regions, annual ET₀ (~2200 mm) far surpassed rainfall, showing that conventional irrigation remains essential. Crops like rice and date palm had the highest water requirements, around 1500–1800 mm and 1400–1600 mm per year, respectively. For example, irrigating one hectare of date palms (~15,000 m³/year) would require roughly 7.7 hectares of SAWH collectors at a daily yield of 0.5 L/m². This study highlights SAWH’s potential as a supplementary water source, particularly during dry seasons, and underscores the role of technology in supporting climate-resilient agriculture and sustainable water management in arid regions.',
    methodology: 'Ratio model (R) comparing SAWH water yield with crop irrigation requirements, spatial and seasonal assessment using ERA5-Land data, crop water demand analysis',
    technologies: ['Water Resource Modeling', 'Crop Water Demand Analysis', 'ERA5-Land Data', 'Geographic Information Systems'],
    duration: 'Hours/Week:6 Weeks/Year:30',
    featured: true
  }
];

// 获取所有研究项目
export const getAllResearches = () => researches;

// 获取特色研究项目
export const getFeaturedResearches = () => researches.filter(research => research.featured);

// 按分类获取研究项目
export const getResearchesByCategory = (category) => 
  researches.filter(research => research.category === category);

// 获取研究项目详情
export const getResearchById = (id) => 
  researches.find(research => research.id === id);

// 获取所有分类
export const getAllCategories = () => {
  const categories = [...new Set(researches.map(research => research.category))];
  return categories;
};